* {
  box-sizing: border-box;
  text-decoration: none;
}
*:focus {
  outline: 0;
}
*:hover {
  text-decoration: none;
}
*:focus {
  outline: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
.link {
  margin-bottom: 0.5em;
}
.link a {
  color: #52c136;
  cursor: pointer;
  font-size: 1.6rem;
  margin-right: 0.4rem;
  position: relative;
  text-decoration: none;
  transition: all 0.3s ease;
}
.link a:after {
  background-color: rgba(82,193,54,0.3);
  bottom: 0;
  content: '';
  height: 1px;
  right: 0;
  position: absolute;
  transition: width 0.4s cubic-bezier(0.7, 0, 0.3, 1);
  width: 0;
}
.link a:hover:after {
  left: 0;
  width: 100%;
}
.link .svg {
  transform: translateY(1px);
}
.link:hover {
  color: #4aae31;
}
html {
