.btn {
  border: none;
  background-color: #52c136;
  border-radius: 5.6rem;
  align-items: center;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  font-family: 'Gordita', 'Arial', 'Helvetica', sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  justify-content: center;
  line-height: 1;
  max-height: 6.4rem;
  padding: 2rem 3.2rem;
  position: relative;
  vertical-align: bottom;
}
.btn:before {
  background-color: #52c136;
  border-radius: 6.4rem;
  content: '';
  left: 0;
  height: 100%;
  position: absolute;
  top: 0;
  transition: all 0.25s ease;
  width: 100%;
  z-index: -1;
}
.btn:hover:before {
  left: -3%;
  height: 110%;
  top: -5%;
  width: 106%;
}
.btn--small {
  padding: 1.6rem 2.4rem;
}
.btn--secondary {
  background-color: #e5f6e1;
  color: #52c136;
}
.btn--secondary:before {
  background-color: #d5f1cf;
}
.btn--transparent {
  background-color: rgba(255,255,255,0.2);
}
.btn--full {
  width: 100%;
}
.btn + .btn {
  margin-left: 2rem;
}
.btn__plate {
  position: absolute;
  top: -1.2rem;
  right: 1.6rem;
}
.text-button {
