.checkbox {
  align-items: center;
  cursor: pointer;
  display: flex;
  margin-bottom: 1em;
  user-select: none;
}
.checkbox__label {
  background: transparent;
  border-radius: 4px;
  border: 1px solid #dbe1e9;
  cursor: pointer;
  height: 1.8rem;
  width: 1.8rem;
  display: inline-block;
  flex-shrink: 0;
  margin-right: 0.4rem;
  position: relative;
  transform: translateY(-1px);
  z-index: 1;
}
.checkbox__label:after {
  color: #283135;
  content: url("../../img/checkbox.png");
  display: none;
  font-size: 1.3rem;
  left: 2px;
  position: absolute;
  top: 2px;
  z-index: 10;
}
.checkbox__text {
  display: inline-block;
}
.checkbox .link {
  display: inline-block;
  margin-bottom: 0;
}
.checkbox input {
  display: none;
}
.checkbox input:checked + .checkbox__label:after {
  display: block;
}
.radio {
  cursor: pointer;
  display: block;
  margin-bottom: 1rem;
  user-select: none;
}
.radio__label {
  background: transparent;
  border-radius: 50%;
  border: 1px solid #dcdcdc;
  cursor: pointer;
  height: 2.4rem;
  width: 2.4rem;
  display: inline-block;
  margin-right: 1rem;
  position: relative;
  vertical-align: middle;
  z-index: 1;
}
.radio__label:after {
  background-color: #52c136;
  border-radius: 50%;
  height: 1.4rem;
  width: 1.4rem;
  color: #fff;
  content: '';
  display: none;
  font-size: 1.5rem;
  left: 4px;
  position: absolute;
  top: 4px;
  z-index: 10;
}
.radio input {
  display: none;
}
.radio input:checked + .radio__label {
  background-color: #fff;
  border-color: #52c136;
  box-shadow: 0 0 0 1px #52c136;
}
.radio input:checked + .radio__label:after {
  display: block;
}
.radio label {
  color: #283135;
  display: inline-block;
