.modal--one-click-buy .ocb {
  align-items: center;
  background-color: #f4f4f4;
  border-top: 1px solid #dbe1e9;
  border-bottom: 1px solid #dbe1e9;
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
  margin-left: -4rem;
  margin-right: -4rem;
  padding: 1.6rem 4rem;
}
.modal--one-click-buy .ocb__img {
  border-radius: 4px;
  max-height: 6rem;
  overflow: hidden;
  width: 8rem;
}
.modal--one-click-buy .ocb__img img {
  display: block;
  width: 100%;
}
.modal--one-click-buy .ocb__container {
  max-width: 45%;
}
.modal--one-click-buy .ocb__container p {
  margin-bottom: 0;
}
.modal--one-click-buy .ocb__tech {
  color: #949494;
  font-size: 1.4rem;
  margin-bottom: 0.4em;
}
.modal--one-click-buy .ocb__price {
  font-size: 2rem;
}
.modal--big-card {
  max-width: 1350px;
  top: 0;
  width: 94%;
}
.modal--big-card .big-card__right {
  position: relative;
  top: 2rem;
}
.modal--city .city {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 2rem;
}
.modal--city .city__container:not(:last-child) {
  margin-right: 4.8rem;
}
.mfp-fade.mfp-bg {
  opacity: 0;
  transition: all 0.2s ease-out;
}
.mfp-fade.mfp-bg.mfp-ready {
  opacity: 0.8;
}
.mfp-fade.mfp-bg.mfp-removing {
  transition: all 0.2s ease-out 0.2s;
  opacity: 0;
}
.mfp-fade.mfp-wrap .mfp-content {
  opacity: 0;
  transform: translateY(10rem);
  transition: all 0.3s ease-out 0.2s;
}
.mfp-fade.mfp-wrap.mfp-ready .mfp-content {
  opacity: 1;
  transform: none;
}
.mfp-fade.mfp-wrap.mfp-removing .mfp-content {
  opacity: 0;
  transform: translateY(10rem);
  transition: all 0.3s ease-out;
}
.mfp-ready .modal {
  display: block;
}
@media (max-width: 1000px) {
  .modal--big-card {
    top: 2rem;
  }
}
@media (max-width: 767px) {
  .mfp-container {
    padding: 0;
  }
  .modal {
    padding: 2.4rem 4%;
    right: 0;
    width: 94%;
  }
  .modal .mfp-close,
  .modal .mfp-close:active {
    font-size: 4rem;
    opacity: 1;
    top: 1.4rem;
    right: 0.4rem;
  }
  .modal__header {
    font-size: 2.4rem;
  }
  .modal--one-click-buy .ocb {
    margin-left: -4%;
    margin-right: -4%;
    padding: 1.6rem 4%;
  }
}
@media (max-width: 500px) {
  .modal--one-click-buy .ocb {
    flex-wrap: wrap;
  }
  .modal--one-click-buy .ocb__img {
    margin-bottom: 2rem;
    margin-right: 1rem;
    order: 1;
    width: 50%;
  }
  .modal--one-click-buy .ocb__name {
    display: block;
    font-size: 1.4rem;
    width: 100%;
  }
  .modal--one-click-buy .ocb__info {
    order: 2;
    width: 40%;
  }
  .modal--one-click-buy .ocb__container {
    order: 3;
    flex-shrink: 0;
    flex-basis: 100%;
    width: 100%;
  }
}
@media (max-width: 370px) {
  .modal__header {
    font-size: 2rem;
  }
}
html,
body {
  background-color: #f4f4f8;
  height: 100%;
  overflow: hidden;
  width: 100%;
}
#particle-slider {
  height: 100vh;
  width: 100vw;
  position: absolute;
  z-index: 200;
}
.header {
  display: flex;
  justify-content: center;
  position: absolute;
}
.circle {
  border: 4px solid rgba(40,49,53,0.1);
  border-radius: 50%;
  bottom: 0;
  height: 64rem;
  width: 64rem;
  content: '';
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  top: 0;
}
.circle .link {
  align-items: center;
  cursor: pointer;
  display: flex;
  margin-bottom: 0;
  position: absolute;
  z-index: 200;
}
.circle .link:nth-child(1) {
  left: -78px;
  text-align: right;
  top: 55px;
}
.circle .link:nth-child(2) {
  left: 500px;
  top: 55px;
}
.circle .link:nth-child(3) {
  left: 500px;
  top: 542px;
}
.circle .link:nth-child(4) {
  left: -60px;
  text-align: right;
  top: 542px;
}
.circle .link .button {
  align-items: center;
  border-radius: 32px;
  display: flex;
  padding: 8px;
  padding-left: 16px;
  position: relative;
  z-index: 300;
}
.circle .link .button + .link__text {
  margin-left: 1.2rem;
}
.circle .link__img + .link__text {
  margin-left: 1.2rem;
}
.circle .link__text {
  position: relative;
  z-index: 20;
}
.circle .link__text + .link__img {
  margin-left: 1.2rem;
}
.circle .link__text + .plus,
.circle .link__text + .button {
  margin-left: 1.2rem;
}
.circle .link__big-img {
  opacity: 0;
  left: 88px;
  position: absolute;
  top: -80px;
  transform: scale(0.9);
  transition: all 0.3s ease;
  z-index: 0;
}
.circle .link:hover {
  color: #283135;
}
.circle .link:hover .link__big-img {
  opacity: 1;
  transform: none;
}
.circle .link:hover .link__text {
  color: #fff;
}
.circle .link:hover .button {
  background-color: #054798;
}
.plus {
  align-items: center;
  background-color: #f4f4f8;
  border: 3px solid #054798;
  height: 36px;
  width: 36px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
}
.small-offset {
  margin-bottom: 1em;
}
.text-gray {
  color: #949494;
}
@media (max-width: 1200px) {
  body.body--cursor-pointer {
    cursor: pointer;
  }
  .grid--container {
    max-width: 96%;
  }
}
